import React, { useEffect, useState } from 'react'
import { Button, Card, Col, Image, ListGroup, Row ,Form} from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { useParams,useNavigate } from 'react-router-dom'
import Rating from '../components/Rating'
import { useDispatch,useSelector } from 'react-redux'
import { createProductReview, listProductDetail } from '../actions/productAction'
import Loader from '../components/Loader'
import Message from '../components/Message'
import { PRODUCT_CREATE_REVIEW_RESET } from '../constants/productConstants'

function ProductScreen(props) {
    // const [product, setProduct] = useState({})
    const [qty, setQty] = useState(1)
    const [rating, setRating] = useState(0)
    const [comment, setComment] = useState('')

    const dispatch = useDispatch();
    const productDetail = useSelector(state => state.productDetail)
    const { loading, error, product } = productDetail;

    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin


    const productReviewCreate = useSelector(state => state.productReviewCreate)
    const {
        loading: loadingProductReview,
        error: errorProductReview,
        success: successProductReview,
    } = productReviewCreate

    const navigate = useNavigate();
    
const { id } = useParams();
    useEffect(() => {
        if (successProductReview) {
            setRating(0)
            setComment('')
            dispatch({ type: PRODUCT_CREATE_REVIEW_RESET })
        }

        dispatch(listProductDetail(id))

    }, [dispatch, successProductReview,id])

  
    const addtoCartHandler = (e) => {
        e.preventDefault();
        navigate(`/cart/${id}?qty=${qty}`)
}
   const submitHandler = (e) => {
        e.preventDefault()
        dispatch(createProductReview(
            id, {
            rating,
            comment
        }
        ))
    }
//   const product = products.find((product) => product._id === id);
  return (
    <div>
          <Link to={`/`} className='btn btn-light my-3'>Go Back</Link>
          {loading ? <Loader /> : error ? <Message variant='danger'>{error}</Message> : (
               <div>
        <Row>
          <Col md={6}>
            <Image src={product && product.image}/>
          </Col>
          <Col md={3}>
                                    <ListGroup variant="flush">
                                        <ListGroup.Item>
                                            <h3>{product.name}</h3>
                                        </ListGroup.Item>

                                        <ListGroup.Item>
                                            <Rating value={product.rating} text={`${product.num_reviews} reviews`} color={'#f8e825'} />
                                        </ListGroup.Item>

                                        <ListGroup.Item>
                                            Price: ${product.price}
                                        </ListGroup.Item>

                                        <ListGroup.Item>
                                            Description: {product.description}
                                        </ListGroup.Item>
                                    </ListGroup>
                                </Col>
            <Col md={3}>
                                    <Card>
                                        <ListGroup variant='flush'>
                                            <ListGroup.Item>
                                                <Row>
                                                    <Col>Price:</Col>
                                                    <Col>
                                                        <strong>${product.price}</strong>
                                                    </Col>
                                                </Row>
                                            </ListGroup.Item>
                                            <ListGroup.Item>
                                                <Row>
                                                    <Col>Status:</Col>
                                                    <Col>
                                                        {product.count_in_stock > 0 ? 'In Stock' : 'Out of Stock'}
                                                    </Col>
                                                </Row>
                                            </ListGroup.Item>

                                            {product.count_in_stock > 0 && (
                                      <ListGroup.Item>
                                          <Row>
                                              <Col>Qty</Col>
                                              <Col xs="auto" className='my-1'>
                                                  <Form.Control as="select"
                                                      value={qty}
                                                      onChange={(e) => setQty(e.target.value)}>
                                                      {
                                                          [...Array(product.count_in_stock).keys()].map(x => (
                                                              <option key={x + 1} value={x + 1}>
                                                                  {x+1}
                                                              </option>
                                                          ))
                                                      }
                                                  </Form.Control>
                                              </Col>
                                          </Row>
                                      </ListGroup.Item>
                                  )}


                                            <ListGroup.Item>
                                                <Button
                                                    
                                                    className='btn-block'
                                                    disabled={product.count_in_stock === 0}
                                                    type='button' onClick={addtoCartHandler}>
                                                    Add to Cart
                                                </Button>
                                  </ListGroup.Item>
                                  
                                        </ListGroup>
                                    </Card>
                                </Col>
                            </Row>
       <Row>
                                <Col md={6}>
                                    <h4>Reviews</h4>
                                    {product.reviews && product.reviews.length === 0 && <Message variant='info'>No Reviews</Message>}

                                    <ListGroup variant='flush'>
                                        {product.reviews && product.reviews.map((review) => (
                                            <ListGroup.Item key={review._id}>
                                                <strong>{review.name}</strong>
                                                <Rating value={review.rating} color='#f8e825' />
                                                <p>{review.createdAt.substring(0, 10)}</p>
                                                <p>{review.comment}</p>
                                            </ListGroup.Item>
                                        ))}

                                        <ListGroup.Item>
                                            <h4>Write a review</h4>

                                            {loadingProductReview && <Loader />}
                                            {successProductReview && <Message variant='success'>Review Submitted</Message>}
                                            {errorProductReview && <Message variant='danger'>{errorProductReview}</Message>}

                                            {userInfo ? (
                                                <Form onSubmit={submitHandler}>
                                                    <Form.Group controlId='rating'>
                                                        <Form.Label>Rating</Form.Label>
                                                        <Form.Control
                                                            as='select'
                                                            value={rating}
                                                            onChange={(e) => setRating(e.target.value)}
                                                        >
                                                            <option value=''>Select...</option>
                                                            <option value='1'>1 - Poor</option>
                                                            <option value='2'>2 - Fair</option>
                                                            <option value='3'>3 - Good</option>
                                                            <option value='4'>4 - Very Good</option>
                                                            <option value='5'>5 - Excellent</option>
                                                        </Form.Control>
                                                    </Form.Group>

                                                    <Form.Group controlId='comment'>
                                                        <Form.Label>Review</Form.Label>
                                                        <Form.Control
                                                            as='textarea'
                                                            row='5'
                                                            value={comment}
                                                            onChange={(e) => setComment(e.target.value)}
                                                        ></Form.Control>
                                                    </Form.Group>

                                                    <Button
                                                        disabled={loadingProductReview}
                                                        type='submit'
                                                        variant='primary'
                                                    >
                                                        Submit
                                                    </Button>

                                                </Form>
                                            ) : (
                                                    <Message variant='info'>Please <Link to='/login'>login</Link> to write a review</Message>
                                                )}
                                        </ListGroup.Item>
                                    </ListGroup>
                                </Col>
                            </Row>
                        </div>
          )}
     
    </div>
  )
}

export default ProductScreen